.page__body {
  position: relative;
}

.modal {
  display: none;
  position: absolute;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(255, 255, 255, 0.9);
}

.modal--open {
  display: block;
}

.modal__wrap {
  position: fixed;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
  width: 320px;
  background-color: #ffffff;
  padding: 28px 30px 47px;
  margin: 0 auto;
  border-top: 1px solid #c6c6c6;
  border-bottom: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
}
@media (min-width: 768px) {
  .modal__wrap {
    width: 566px;
    padding: 38px 30px 44px;
  }
}

@media (min-width: 768px) {
  .modal__wrap br {
    display: none;
  }
}

.modal__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .modal__title {
    font-size: 30px;
    line-height: 57px;
    margin-bottom: 11px;
  }
}

.modal__subtitle {
  font-size: 17px;
  line-height: 30px;
  text-align: center;
}

.modal__button {
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
  width: 260px;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  padding: 18px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal__button {
    width: 350px;
    display: block;
    margin: 0 auto;
  }
}

.modal__contaner {
  padding: 0;
  border: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 21px auto 31px;
}
@media (min-width: 768px) {
  .modal__contaner {
    width: 350px;
  }
}

.modal__item {
  flex-grow: 1;
}

.modal__size {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  color: #444444;
  padding-top: 16px;
  padding-bottom: 16px;
  box-sizing: border-box;
  border: 2px solid #444444;
  display: block;
  margin: 0 auto;
  text-align: center;
}

.modal__input:checked + .modal__size {
  border: 2px solid #63d1bb;
}

.modal__item:nth-last-child(n+2) {
  margin-right: 5px;
}

.button {
  font: inherit;
  display: block;
  text-align: center;
  color: #444444;
  background-color: #ffffff;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  padding: 15px;
  text-transform: uppercase;
  border: 2px #444444 solid;
  box-sizing: border-box;
}
.button:hover, .button:focus {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
}

.header {
  background-color: #ffffff;
  width: 100%;
  display: block;
  z-index: 10;
  min-width: 320px;
  min-height: 79px;
  margin: 0 auto;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .header {
    min-height: 89px;
    border: none;
    background-color: #f1f1f1;
  }
  .header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 86px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    z-index: -5;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .header {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .header--index {
    background-color: #ffffff;
  }
  .header--index::before {
    top: 86px;
    height: 0;
    z-index: 5;
  }
}
@media (min-width: 1150px) {
